import { __ } from '@wp-plugins/i18n';
import { FormField } from '@wp-plugins/form';
import { FeildStack, FeildStackItem, SectionCard } from '@wp-plugins/components';
import { Divider } from '@wp-plugins/adapters';

import { P2TGCustomRules, And } from '@wp-plugins/wptelegram-shared--core-pro';

import { getFieldLabel, useData } from './../../services';
import { PREFIX } from './constants';
import { Upsell } from './../shared/Upsell';

const send_when_options = [
	{
		value: 'new',
		label: __('A new post is published'),
	},
	{
		value: 'existing',
		label: __('An existing post is updated'),
	},
];

export const Rules: React.FC = () => {
	const {
		api: { rest_namespace },
		uiData: { post_types, rule_types },
	} = useData();

	return (
		<SectionCard title={__('Rules')}>
			<FeildStack>
				<FeildStackItem flexBasis='48%'>
					<FormField
						name={`${PREFIX}.send_when`}
						fieldType='multicheck'
						label={getFieldLabel('send_when')}
						description={__('When the post should be sent to Telegram.')}
						options={send_when_options}
						controlClassName='no-flex'
					/>
				</FeildStackItem>
				<FeildStackItem flexBasis='48%'>
					<FormField
						name={`${PREFIX}.post_types`}
						fieldType='multicheck'
						label={getFieldLabel('post_types')}
						description={__('Which post types should be sent.')}
						options={post_types}
						controlClassName='no-flex'
					/>
				</FeildStackItem>
			</FeildStack>
			<Divider my='0' />
			<And />
			<P2TGCustomRules prefix={PREFIX} rule_types={rule_types} rest_namespace={rest_namespace} />
			<Upsell location='rules' textAlign='center' />
		</SectionCard>
	);
};
