import { __ } from '@wp-plugins/i18n';
import { FormField, FormFieldProps } from '@wp-plugins/form';
import { prefixName } from '@wp-plugins/utilities';

import { getFieldLabel } from '../services';
import type { CommonProps } from './types';

export type MessageTemplateProps = Partial<FormFieldProps<any, any>> & CommonProps;

export const MessageTemplate: React.FC<MessageTemplateProps> = ({ prefix }) => {
	return (
		<FormField
			name={prefixName('message_template', prefix)}
			fieldType='textarea'
			label={getFieldLabel('message_template')}
			description={__('Structure of the message to be sent.')}
			rows={10}
			height='auto'
			spellCheck={false}
		/>
	);
};
