import { assocPath } from 'ramda';

import { __ } from '@wp-plugins/i18n';
import { Link } from '@wp-plugins/adapters';
import { FormField, FormFieldProps } from '@wp-plugins/form';
import { prefixName } from '@wp-plugins/utilities';

import { getFieldLabel } from '../services';
import type { CommonProps } from './types';

export type ParseModeFieldProps = {
	type?: 'BASIC' | 'ADVANCED';
} & Partial<FormFieldProps<any, any>> &
	CommonProps;

export const BASIC_PARSE_MODE_OPTIONS = [
	{
		value: 'none',
		label: __('None'),
	},
	{
		value: 'Markdown',
		label: __('Markdown style'),
	},
	{
		value: 'HTML',
		label: __('HTML style'),
	},
];

export const ADVANCED_PARSE_MODE_OPTIONS = assocPath([1, 'value'], 'MarkdownV2', BASIC_PARSE_MODE_OPTIONS);

export const ParseModeField: React.FC<ParseModeFieldProps> = ({ prefix, type = 'BASIC', ...props }) => {
	const options = 'BASIC' === type ? BASIC_PARSE_MODE_OPTIONS : ADVANCED_PARSE_MODE_OPTIONS;

	return (
		<FormField
			name={prefixName('parse_mode', prefix)}
			fieldType='radio'
			label={getFieldLabel('parse_mode')}
			after={
				<Link
					color='blue.500'
					href='https://core.telegram.org/bots/api/#formatting-options'
					isExternal
					mt='0.3em'
				>
					{__('Learn more')}
				</Link>
			}
			options={options}
			{...props}
		/>
	);
};
