import { Text } from '@wp-plugins/adapters';
import { __ } from '@wp-plugins/i18n';
import { Code } from '@wp-plugins/components';

import type { CommonProps } from './types';
import { MessageTemplate } from './MessageTemplate';

export const NotifyMessageTemplate: React.FC<CommonProps> = ({ prefix }) => {
	return (
		<MessageTemplate
			prefix={prefix}
			after={
				<>
					<Text as='span'>{__('You can use any text, emojis or these macros in any order.')}</Text>
					<br />
					{['{email_subject}', '{email_message}'].map((tag, i) => (
						<Code key={i}>{tag}</Code>
					))}
				</>
			}
		/>
	);
};
