import { Description } from '@wp-plugins/components';
import { __ } from '@wp-plugins/i18n';
import { useWatch } from '@wp-plugins/form';

import { ActiveField, P2TGInstructions, IfActive } from '@wp-plugins/wptelegram-shared--core-pro';

import { PREFIX } from './constants';
import { Destination } from './Destination';
import { IfBotToken } from '../shared/IfBotToken';
import { Rules } from './Rules';
import { MessageSettings } from './MessageSettings';
import { MessageKeyboard } from './MessageKeyboard';
import { Miscellaneous } from './Miscellaneous';
import type { DataShape } from '../../services';

export const P2TG: React.FC = () => {
	const bot_username = useWatch<DataShape, 'bot_username'>({ name: 'bot_username' });

	return (
		<>
			<Description>{__('With this module, you can configure how the posts are sent to Telegram.')}</Description>

			<ActiveField prefix={PREFIX} />

			<IfActive name={`${PREFIX}.active`}>
				<IfBotToken>
					<P2TGInstructions botUsername={bot_username} videoId='m48V-gWz9-o' />
					<Destination />
					<Rules />
					<MessageSettings />
					<MessageKeyboard />
					<Miscellaneous />
				</IfBotToken>
			</IfActive>
		</>
	);
};
