import { useMemo } from 'react';

import { __ } from '@wp-plugins/i18n';

import { AdvancedSettings as SharedAdvancedSettings } from '@wp-plugins/wptelegram-shared--core-pro';

import { useData } from '../../services';
import { PREFIX } from './constants';

export const Advanced: React.FC = () => {
	const { assets, uiData } = useData();

	const log_options = useMemo(
		() => [
			{
				value: 'bot_api',
				label: __('Bot API'),
				viewLink: assets.botApiLogUrl,
			},
			{
				value: 'p2tg',
				label: __('Post to Telegram'),
				viewLink: assets.p2tgLogUrl,
			},
		],
		[assets]
	);

	return <SharedAdvancedSettings prefix={PREFIX} log_options={log_options} debug_info={uiData?.debug_info} />;
};
