import { Stack, StackProps } from '@wp-plugins/adapters';

const direction: StackProps['direction'] = ['column', 'row'];

export const FeildStack: React.FC = ({ children }) => {
	return (
		<Stack direction={direction} flexWrap='wrap' justifyContent='space-between' spacing='1em'>
			{children}
		</Stack>
	);
};
