import { Divider } from '@wp-plugins/adapters';
import { Description, SectionCard } from '@wp-plugins/components';
import { __ } from '@wp-plugins/i18n';
import { useWatch } from '@wp-plugins/form';

import { ActiveField, UserNotifications, NotifyInstructions, IfActive } from '@wp-plugins/wptelegram-shared--core-pro';

import { useData, DataShape } from '../../services';
import { PREFIX } from './constants';
import { IfBotToken } from '../shared/IfBotToken';
import { WatchEmails } from './WatchEmails';
import { MessageSettings } from './MessageSettings';

export const Notify: React.FC = () => {
	const { editProfileUrl } = useData('assets');
	const bot_username = useWatch<DataShape, 'bot_username'>({ name: 'bot_username' });

	return (
		<>
			<Description>
				{__(
					'The module will watch the Email Notifications sent from this site and deliver them to you on Telegram.'
				)}
			</Description>

			<ActiveField prefix={PREFIX} />

			<IfActive name={`${PREFIX}.active`}>
				<IfBotToken>
					<NotifyInstructions botUsername={bot_username} videoId='gVJCtwkorMA' />
					<SectionCard title={__('Notification Settings')}>
						<WatchEmails />
						<Divider mt='2em' />
						<UserNotifications editProfileUrl={editProfileUrl} prefix={PREFIX} />
						<MessageSettings />
					</SectionCard>
					<Divider />
				</IfBotToken>
			</IfActive>
		</>
	);
};
